import React, {useEffect, useState} from 'react';
import RealTime from "../features/RealTime";
import {useCookies} from "react-cookie";
import ModalForm from "./ModalForm";
import Weather from "../features/Weather";
import AudioPlayer from "../features/AudioPlayer/AudioPlayer";
import Quotes from "../features/Quotes/Quotes";

const Main = ({dayPart, setDayPart}) => {
    const [cookies, setCookie] = useCookies(["username"])
    const [modals, setModals] = useState('')
    const [showModal, setShowModal] = useState(false)


    useEffect(() => {
        const hours = new Date().getHours()
        if (hours >= 6 && hours < 11) setDayPart(' ')
        else if (hours >= 11 && hours < 17) setDayPart(' ')
        else if (hours >= 17 && hours < 21) setDayPart(' ')
        else setDayPart(' ')
    }, [])

    function handleUsernameChange(e) {
        setCookie("username", e.target.value)
    }

    function changeModalContent(name) {
        setShowModal(true);
        setModals(name)
    }

    function renderComponent() {
        switch (modals) {
            case "audio":
                return <AudioPlayer/>
            case "quotes":
                return <Quotes/>
            case "weather":
                return <Weather/>
            default: return;
        }
    }

    return (
        <main>
            <ul className="main-content">
                <li className="welcome-phrase show">
                    <span>{dayPart},</span>
                    <input
                        className="name-field"
                        value={cookies.username || ''}
                        onChange={handleUsernameChange}
                        type="text"
                        placeholder=""/>
                </li>
            </ul>
            <ul className="modals-nav">
                <li>
                    <button onClick={() => changeModalContent('weather')}> </button>
                </li>
                <li>
                    <button onClick={() => changeModalContent('audio')}> </button>
                </li>
                <li>
                    <button onClick={() => changeModalContent('quotes')}>, ,  </button>
                </li>
            </ul>
            {/*{showModal &&*/}
            <ModalForm showModal={showModal} setShowModal={setShowModal}>
                {renderComponent()}
            </ModalForm>
            {/*}*/}

        </main>
    );
};

export default Main;